.b-project {
  /*min-height: 175px; */
  position: relative;
  display: block;
  text-decoration: none;
  //overflow: hidden
  &:after {
    display: block;
    content: "";
    width: 100%;
    padding-top: 62%; }

  .b-project-container {
    &:before {
      transition: opacity .5s;
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      opacity: 0;
      background: #00f; } }

  &.__animate {
    z-index: 100;

    .b-project-container {
      &:before {
        opacity: 1; } }

    .b-project-info {
      opacity: 0 !important;
      pointer-events: none;
      transition-delay: .3s; }

    .b-project-info-loader {
      opacity: 1;
      width: 4000px;
      height: 2500px;
      box-shadow: 0 0 25px #00f; } } }

.b-project-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: none no-repeat transparent center center;
  background-size: cover; }

.b-project {
