    justify-content: center;
    text-align: left;
    color: #fff;
    background: rgba(0, 0, 255, 0.8);
    width: 100%;
    height: 100%;
    //overflow: hidden

    //&:hover
 }    //opacity: 1

  &:hover {
    .b-project-info {
      opacity: 1; } } }

