@keyframes project-list-caption {
  0% {
    transform: translate(0, 22px);
    opacity: 0.2; }

  40% {
    transform: translate(0, 4px);
    opacity: 0.5; }

  60% {
    margin-top: 0;
    opacity: 0.5; }

  80% {
    margin-top: 0;
    opacity: 0.8; }

  100% {
    margin-top: 0;
    opacity: 1; } }

@keyframes project-list-caption-hide {
  100% {
    transform: translate(0, 22px);
    opacity: 0.2; }

  60% {
    transform: translate(0, 4px);
    opacity: 0.5; }

  40% {
    margin-top: 0;
    opacity: 0.5; }

  20% {
    margin-top: 0;
    opacity: 0.8; }

  0% {
    margin-top: 0;
    opacity: 1; } }

@keyframes text_bottom2top {
  0% {
    transform: translate3d(0, 100%, 0);
    opacity: 0; }

  100% {
    transform: translate3d(0, 0, 0);
    opacity: .9999; } }

@keyframes text_opacity {
  0% {
    opacity: 0; }

  100% {
    opacity: 1; } }

@keyframes tag_item {
  0% {
    opacity: 0;
    transform: translate3d(0, 10px, 0); }

  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

@keyframes arrow_anim {
  0% {
    transform: translate3d(0, 2rem, 0);
    opacity: 0; }

  100% {
    transform: translate3d(0, 0, 0);
    opacity: .9999; } }

