svg.logo {
  width: 200px;
  height: 46px;
  position: relative;
  z-index: 10;
  cursor: pointer;
  .logo-circle {
    transition: fill .3s;
    fill: #0000fe;
    circle {
      transition: fill .3s;
      fill: #0000fe; } }
  .logo-text > * {
    transition: fill .3s, opacity .3s;
    //fill: #231f20
    fill: #0000fe;
    opacity: 0; } }

.intro svg.logo .logo-text > * {
  opacity: 1; }

svg.logo.logo-white {
  .logo-circle {
    fill: #fff;
    circle {
      fill: #fff; } }
  .logo-text > * {
    fill: #fff; } }

body {
  &.__intro:not(.__fade_in_nav) {
    .navbar-custom svg.logo {
      .logo-circle {
        fill: #fff !important;
        circle {
          fill: #fff !important; } }
      .logo-text > * {
        fill: #fff !important; } }
    .navbar-toggle {
      color: #fff; } }
  &.home.__intro svg.logo .logo-text > * {
    opacity: 1; }
  &.is-open {
    @media (max-width: 768px) {
      .navbar-header {
        position: relative;
        z-index: 999; }
      .navbar-custom svg.logo {
        .logo-circle {
          fill: #0000fe !important;
          circle {
            fill: #0000fe !important; } } } } } }

@media (min-width: 768px) {
  svg.logo {
    width: 41rem;
    height: 9.5rem; } }
