.navbar-custom {
  transition: transform .3s, opacity .3s;
  margin-top: 27px;
  margin-bottom: 0;
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  right: 0;
  // height: 0
  padding: 0;
  min-height: 1px;
  // > .container
  //   height: 0
  //   > .row
  //     height: 0

  &.nav-up {
    transform: translate(0, -180%); }

  //.__fade_in_nav &,
  .__leave &,
  .__anim_next & {
    transition: transform .3s 5s, opacity .3s;

    .navbar-brand svg {
      opacity: 0 !important; } }

  //.__start_anim &,
  &.__invis {
    &.nav-down {
      transform: translate(0, 0);
      transition: transform 0s, opacity 0s; }

    .navbar-header {
      opacity: 0;
      transition: opacity 0s; }

    body:not(.__intro) & {
      &.nav-down {
        transition: transform 0s, opacity .3s .7s; }

      .navbar-header {
        transition: opacity .3s .7s; } } }

  body.__fade_in_nav & {
    transition: transform .3s 0s, opacity .3s .8s;
    opacity: 0; }

  .__anim_wave body & {
    .navbar-brand svg,
    .navbar-header {
      opacity: 1 !important; }

    //&.nav-down
    //svg.logo .logo-circle
    //  -webkit-transition: fill .3s
    //  -o-transition: fill .3s
    //  transition: fill .3s
    //  fill: #0000fe !important


    //.navbar-header
    //opacity: 0
 }    //transition: opacity .3s .8s

  //animation-duration: .3s
  ////animation-delay: .3s
  ////animation-timing-function: linear
  ////animation-iteration-count: 1
  ////animation-fill-mode: forwards
  ////animation-direction: normal
  ////pointer-events: none
  ////animation-name: logo_next

  .navbar-brand {
    height: auto;
    display: inline-block;
    padding: 0;
    line-height: 0;
    margin-left: 20px !important;
    position: relative;
    z-index: 999;
    outline: none !important; }
  &.in .navbar-brand {
    position: static; }
  .b-menu-overlay {
    z-index: 10;
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    left: 0;
    background: rgba(0, 0, 0, 0.25);
    transition: 0.6s cubic-bezier(0.19, 1, 0.22, 1);
    opacity: 0;
    visibility: hidden; }
  .b-menu {
    display: block;
    position: fixed;
    transition: 0.8s cubic-bezier(0.19, 1, 0.22, 1);
    top: 0;
    right: 0;
    height: 100vh;
    background: #fff;
    padding: 0 20px;
    z-index: 998;
    width: 100%;
    left: 100%;
    margin-left: 15px; }
  &.in {
    .b-menu {
      left: 0;
      margin-left: 0; }
    .b-menu-overlay {
      opacity: 1;
      visibility: visible; } }
  .b-menu-container {
    position: relative; }
  .b-menu {
    .b-menu-body {
      display: flex;
      height: 100vh;
      align-items: center;
      justify-content: flex-start;
      background: #fff;
      margin-top: -6rem; }
    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      font-size: 2.4rem;
      letter-spacing: 0.15rem;
      text-transform: uppercase;
