.navbar-custom {
  .navbar-collapse {
    position: relative;
    z-index: 999;
    height: 46px !important;
    border: none;
    box-shadow: none;
    width: 10rem;
    float: right; }
  .navbar-toggle {
    color: #00f;
    display: block;
    background: transparent;
    border: none;
    font: 0/0 a;
    border-radius: 0;
    position: absolute;
    z-index: 999;
    width: 84px;
    height: 73px;
    top: 50%;
    right: -10px;
    margin: -36px 0 0;
    padding: 0; }
  &.in .navbar-toggle {
    color: #00f !important; }
  .navbar-toggle .navbar-toggle-icon {
    width: 74px;
    height: 74px;
    position: relative;
    transform: rotate(0deg);
    transition: .3s ease-in-out, 0s color, 0s background-color;
    cursor: pointer;
    padding: 0;
    span {
      display: block;
      position: absolute;
      height: 3px;
      width: 4rem;
      background: currentColor;
      border-radius: 0;
      opacity: 1;
      left: 50%;
      top: 50%;
      margin-left: -2rem;
      margin-top: -0.7rem;
      transform: rotate(0deg);
      transition: all .25s ease-in-out, color 0s, background-color 0s;
      &:nth-child(1) {
        transform-origin: left center; }
      &:nth-child(2) {
        margin-top: 0.7rem;
        transform-origin: left center; } } }
  &.in .navbar-toggle .navbar-toggle-icon span {
    margin-top: -1.4rem;
    margin-left: -1.5rem;
    &:nth-child(1) {
      transform: rotate(45deg); }
    &:nth-child(2) {
      transform: rotate(-45deg);
      margin-top: 1.4rem; } }
  .navbar-toggle {
    &:focus, &:active {
      outline: none; } } }

@media (min-width: 768px) {
  .navbar-custom {
    margin-top: 5.7rem;
    .navbar-collapse {
      height: 9.5rem !important; } } }
