.navbar-brand span {
  will-change: transform;
  display: block;
  transform: scale(1);
  opacity: 0;
  background: #0000a0;
  position: absolute;
  left: 2.5em;
  top: 38%;
  border-radius: 50%;
  width: 2em;
  height: 2em;
  font-size: 100px;
  transition: transform .7s linear, background .7s linear, box-shadow .7s linear, opacity 1s 0s;
  z-index: 150;
  cursor: default;
  pointer-events: none;
}

.navbar-brand.__anim span {
  transition: transform .7s linear, background .7s linear, box-shadow .7s linear, opacity 0s .1s;
  transform: scale(80);
  opacity: 1;
  box-shadow: 0 0 1rem 1rem #0000fe;
  background: #0000fe;
}

.__skip-circle .navbar-brand.__anim span {
  transform: scale(1);
  opacity: 0;
  box-shadow: none;
}

.navbar-circle {
  will-change: transform;
  display: block;
  transform: scale(0);
  width: 5rem;
  height: 5rem;
  position: fixed;
  left: 4rem;
  top: 7rem;
  border-radius: 100%;
