@keyframes project-list-caption {
  0% {
    transform: translate(0, 22px);
    opacity: 0.2;
  }
  40% {
    transform: translate(0, 4px);
    opacity: 0.5;
  }
  60% {
    margin-top: 0;
    opacity: 0.5;
  }
  80% {
    margin-top: 0;
    opacity: 0.8;
  }
  100% {
    margin-top: 0;
    opacity: 1;
  }
}

@keyframes project-list-caption-hide {
  100% {
    transform: translate(0, 22px);
    opacity: 0.2;
  }
  60% {
    transform: translate(0, 4px);
    opacity: 0.5;
  }
  40% {
    margin-top: 0;
    opacity: 0.5;
  }
  20% {
    margin-top: 0;
    opacity: 0.8;
  }
  0% {
    margin-top: 0;
    opacity: 1;
  }
}

@keyframes text_bottom2top {
  0% {
    transform: translate3d(0, 100%, 0);
    opacity: 0;
  }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: .9999;
  }
}

@keyframes text_opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes tag_item {
  0% {
    opacity: 0;
    transform: translate3d(0, 10px, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes arrow_anim {
  0% {
    transform: translate3d(0, 2rem, 0);
    opacity: 0;
  }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: .9999;
  }
}

@keyframes logo_next {
  100% {
    opacity: 1;
  }
}

@keyframes logo_anim {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(80);
  }
}

@keyframes logo_anim_back {
  100% {
    transform: scale(80);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes logo_anim_big {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(200);
  }
}

@keyframes logo_anim_back_big {
  100% {
    transform: scale(200);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes load_project {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1);
  }
}

@keyframes intro_bg {
  0% {
    background: #fff;
  }
  100% {
    background: #00f;
  }
}

@keyframes start_scale {
  0% {
    transform: scale(0);
  }
  30% {
    transform: scale(1);
  }
  99% {
    transform: scale(1);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes start_c1 {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: -5.6rem;
  }
}

@keyframes start_c2 {
  0% {
    box-shadow: 0 0 0 0 #00f;
    transform: scale(1);
  }
  40% {
    box-shadow: 0 0 3rem 2rem #00f;
    transform: scale(1);
  }
  60% {
    box-shadow: 0 0 3rem 2rem #00f;
    transform: scale(1);
  }
  99% {
    box-shadow: 0 0 3rem 2rem #00f;
    transform: scale(18);
  }
  100% {
    display: none;
    box-shadow: 0 0 3rem 2rem #00f;
    transform: scale(18);
  }
}

@media (max-width: 768px) {
  @keyframes start_c1 {
    0% {
      margin-left: 0;
    }
    100% {
      margin-left: -3rem;
    }
  }
  @keyframes start_c2 {
    0% {
      box-shadow: 0 0 0 0 #00f;
      transform: scale(1);
    }
    40% {
      box-shadow: 0 0 4rem 3rem #00f;
      transform: scale(1);
    }
    60% {
      box-shadow: 0 0 4rem 3rem #00f;
      transform: scale(1);
    }
    99% {
      box-shadow: 0 0 4rem 3rem #00f;
      transform: scale(18);
    }
    100% {
      display: none;
      box-shadow: 0 0 4rem 3rem #00f;
      transform: scale(18);
    }
  }
}

@keyframes next_project {
  60% {
    transform: scale(1, 1);
  }
  100% {
    transform: scale(1, 1);
  }
}

